.tweeting {
    display: flex;
    justify-content: center;
    align-items: center;
}

.write {
    font-size: 14px;
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    outline: 10px;
    border: none;
    border-radius: 15px;
    margin-right: 20px;
}

.write::placeholder {
    opacity: 0.5;
}

.upload {
    color:#ffffff;
    background-color: #e9fac8;
    font-size: 20px;

    padding: 4.5px;
    border: none;
    border-radius: 15px;
}

.upload:hover{
    background-color:#d8f5a2;
}

