.login {
    display: flex;
    flex-direction: column;
    width : 384px;
    margin: 0 auto;
}

.submit {
    font-size: 14px;
    padding: 10px;
    background:#f1f3f5;
    border : none;
    background-color: #e7f5ff;
    border-radius: 100px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
}

.submit:hover {
    background-color: #f4fce3;
}

span {
    font-size: 14px;
    margin: 0 auto;
}

.email {
    border: none;
    padding: 10px;
    margin-bottom: 0.5rem;
}

.pw {
    border: none;
    padding: 10px;
}

.email:hover,
.pw:hover {
    background-color: #f8f9fa;
    border-radius: 15px;
}
