.new{
    font-size: 16px;
    padding: 16px;
    border : none;
    border-radius: 100px;
    
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3),  0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-top: 2rem;
}

.new:hover {
    border : none;
}

.newprofile{
    font-size: 12px;
    padding: 16px;
    border : none;
    border-radius: 100px;

    display: flex;
    margin: 0 auto;
    margin-top: 3rem;
}

.newprofile:hover {
    background-color:#bbe1ff;
}

.logout{
    font-size: 12px;
    padding: 16px;
    border : none;
    border-radius: 100px;

    display: flex;
    margin: 0 auto;
    margin-top: 1rem;
}

.logout:hover {
    background-color: rgb(255, 166, 151);
}