body {
    background: #f8f9fa;
}

.root {
    background: white;
    padding: 16px;
    border-radius: 20px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3),  0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    width: 512px;
    margin: 0 auto;
    margin-top: 11rem;
}

.ggbutton,
.gitbutton {
    font-size: 14px;
    padding: 16px;
    background:#f1f3f5;
    border : none;
    background-color: #f4fce3;
    border-radius: 100px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-top: 1rem;
}

.buttons {
    margin-top: 1.5rem;
    border-top: 1px solid #f1f3f5;
}

.ggbutton {
    margin-top: 1.5rem;
}

div div button:hover {
    background: #e7f5ff;
}